.filter-actions{
  z-index: 10;
  top: 100%; 
  left: 0; 
  position: absolute; 
  background-color: #262837;
  display: flex;
  gap: 2em;
  width: fit-content;
  padding: 1em; 
  border-radius: 8px; 
  box-shadow: 18px 10px 56px 10px rgba(2, 2, 3, 0.9);
  -webkit-box-shadow: 18px 10px 56px 10px rgba(16, 14, 27, 0.9);
  -moz-box-shadow: 18px 10px 56px 10px rgba(0, 0, 0, 0.9);
}

.filter-actions label select{
  display: grid;
  appearance: none;
  background-color: #353748;
  border: none;
  padding: .5em;
  border-radius: 4px;
  margin: 0;
  color: #FAFAFE;
  width: fit-content;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  outline: none;
  cursor: pointer;
  background-image: url(../../library/img/arrow-down.svg);
  background-repeat: no-repeat;
  background-position: right 10px top 50%;
}
.filter-actions label select option{
    color: #FAFAFE !important;
}

.heuristic select{
  width: 370px !important; 
}

.severity select{
  width: 230px !important; 

}

.filter-actions label{
  display: flex; 
  flex-direction: column;
  gap: .5em; 
}
.filter-actions label span{
  color: #FAFAFE; 
}

